footer {
    h3 {
        margin-bottom: 10px;
        font-size: 28px;
    }

    padding: 15px 30px;

    * {
        color: white;
    }

    background-color: #2d383a;

    .content {
        flex-direction: row-reverse;
        justify-content: space-between;
    }

    .svg {
        width: 25px;
        height: 25px;
        margin: 0px 5px;
        fill: white;
    }

    ul {
        list-style-type: none;
        padding: 0;
        font-size: 18px;

        li {
            margin-bottom: 3px;

            a {
                &:hover {
                    color: #f79898;
                }
            }
        }
    }

    .copyrights {
        text-align: center;
    }
}

@media(max-width:500px) {
    footer {
        .content {
            flex-direction: column;
        }
    }
}