.items-list-cart {
    width: 50%;
}

.cart-container {}

.order-preview-container {
    width: 50%;
    max-width: 700px;
    margin-top: 30px;
    height: 229px;
    border-radius: 3px;

    img {
        width: 300px;
        object-fit: cover;
        height: 100%;
    }

}


.container-order-details {
    justify-content: center;
}

.item-price-cart {
    margin-top: 27px;
    width: 7%;
}

.order-details {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-width: 150px;
    padding: 15px;
}

.cancel-edit-cart-item {
    width: 7%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;

    a {
        margin-top: 10px;
    }

    .delete-svg {
        cursor: pointer;
    }
}

.phone {

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    input[type=number] {
        -moz-appearance: textfield;
    }
}

.total-price {
    font-size: 1.5rem;
    font-weight: bold;
    margin: 25px 0;

}




@media (max-width: 450px) {

    .order-list {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .order-preview-container {
        width: 300px;
        margin-top: 30px;
        height: 229px;
        border-radius: 3px;

        img {
            width: 200px;
            object-fit: cover;
            height: 100%;
        }

        .order-details {
            width: 50%;
        }

    }

}